import { React, useEffect } from 'react'
import { graphql } from 'gatsby'
import { Helmet } from 'react-helmet'

import BreadcrumbNav from '../components/breadcrumb-nav'
import TabNav from '../components/tab-nav.js'
import Layout from '../components/layout'
import MyJumbotron from '../components/jumbotron'
import {
  flattenFileNode,
  matterNameFromMatter,
  classeNameFromClasse,
  classeWikidataURIFromClasse,
} from '../helpers'

export default qcuPage

function qcuPage({ data, location }) {
  const qcu = flattenFileNode(data.markdownRemark)

  const title = `Quiz de ${classeNameFromClasse(
    qcu.classe
  )} en ${matterNameFromMatter(qcu.matter)}: ${qcu.chapter}`

  const description = `Quiz de ${classeNameFromClasse(
    qcu.classe
  )} en ${matterNameFromMatter(qcu.matter)} intitulé: «${qcu.chapter}»`

  // type
  const type = `quiz`

  // chemin vers le cours pour référence
  const currentCoursePath = location.pathname
    .substring(0, location.pathname.length - type.length)
    .replace(/^\/|\/$/g, ``)

  // importe le web-compoenent de quiz
  useEffect(() => {
    import(`wc-quiz`)
  }, [])

  return (
    <Layout location={location} data={data}>
      <Helmet title={`${title}`} htmlAttributes={{ class: `qcu` }}>
        <script type="application/ld+json">{`
         {"@context": "http://schema.org/",
          "@type": "exercice",
          "name": "${qcu.part} | ${qcu.chapter}",
          "description": "${description}",
          "learningResourceType": [
            "lesson"    ],
          "provider": {
            "@type": "schema:EducationalOrganization",
            "name": "lyceum.fr"
          },
          "audience": {
            "@type": "EducationalAudience",
            "éducationRole": "student"
          },
          "educationalAlignment": {
            "@type": "AlignmentObject",
            "alignmentType": "educationalLevel",
            "educationalFramework": "French Grade Levels",
            "targetName": "${classeNameFromClasse(qcu.classe)}",
            "@id": "${classeWikidataURIFromClasse(qcu.classe)}"
          },
          "url": "${location.href}"
        }
    `}</script>
      </Helmet>
      <BreadcrumbNav node={qcu} />
      <TabNav currentCoursePath={currentCoursePath} type={type} />
      <MyJumbotron>
        <h1>Quiz</h1>
        <strong>{`Chapitre ${qcu.nChapter}: ${qcu.chapter}`}</strong>
      </MyJumbotron>
      <wc-quiz
        title=" "
        dangerouslySetInnerHTML={{ __html: qcu.rawMarkdownBody }}
      ></wc-quiz>
    </Layout>
  )
}

export const query = graphql`
  query MDqcuQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      rawMarkdownBody
      fields {
        classe
        chapter
        matter
        nChapter
        part
        nPart
        type
      }
    }
  }
`
